
import Vue from 'vue'
import { pages } from '@/store'
import {
  faListUl,
  faCalculator,
  faCartPlus,
  faTriangleExclamation,
  faLayerGroup,
  faPlus,
  faQrcode,
} from '@fortawesome/free-solid-svg-icons'

export default Vue.extend({
  computed: {
    icons() {
      return [faListUl, faPlus, faCalculator, faCartPlus, faLayerGroup, faTriangleExclamation, faQrcode]
    },
    page() {
      return pages.$page
    },
  },
})
